.theme-dark {
	color-scheme: dark;
	--background-primary: #0c0d0f;
	--background-primary-rgb: 0 0 0;
	--background-primary-alt: #030303;
	--background-secondary: #111111;
	--background-secondary: #1a1a1a;
	--background-secondary-alt: #151515;
	--background-modifier-border: #000000;
	--background-modifier-form-field: rgba(0, 0, 0, 0.3);
	--background-modifier-form-field-highlighted: rgba(0, 0, 0, 0.22);
	--background-modifier-box-shadow: rgba(0, 0, 0, 0.3);
	--background-modifier-success: #197300;
	--background-modifier-success-rgb: 25 115 0;
	--background-modifier-error: #3d0000;
	--background-modifier-error-rgb: 61 0 0;
	--background-modifier-error-hover: #470000;
	--background-modifier-cover: rgba(0, 0, 0, 0.8);
	--text-accent: #aa00bb;
	--text-accent-hover: orange;
	--text-normal: #ffffff;
	--text-muted: #aaa;
	--text-muted-rgb: 153 153 153;
	--text-faint: #666;
	--text-error: #ff3333;
	--text-error-hover: #990000;
	--text-highlight-bg: rgba(255, 255, 0, 0.4);
	--text-highlight-bg-active: rgba(255, 128, 0, 0.4);
	--text-selection: #aaa;
	--text-on-accent: #dcddde;
	--interactive-normal: #2a2a2a;
	--interactive-hover: #303030;
	--interactive-accent: #fff;
	--interactive-accent-rgb: 72 54 153;
	--interactive-accent-hover: #eee;
	--interactive-success: #197300;
	--scrollbar-active-thumb-bg: red;
	--scrollbar-bg: green;
	--scrollbar-thumb-bg: blue;
	--highlight-mix-blend-mode: lighten;
}
